.advantage {
  color: black;
  background-color: white;
  text-transform: uppercase;
  font-family: @font-family;
  /*  font-family: "PT Sans";*/
}

@media (min-width: @tablet-width) {
  .advantage {
    color: black;
    background-color: white;
  }
}

.advantage_flexOrder1,
.advantage_flexOrder2,
.advantage_flexOrder3 {
  /* foo */
}

@media (min-width: @tablet-width) {
  .advantage_flexOrder1 {
    order: 1;
  }

  .advantage_flexOrder2 {
    order: 2;
  }

  .advantage_flexOrder3 {
    order: 3;
  }
}

.advantage__list-vertical {
  margin: 0;
  padding: 0;
  list-style: none;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: @tablet-width) {
  .advantage__list-vertical {
    margin: 0 auto;
    padding: 0;
    list-style: none;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-end;
  }
}

.advantage__item-vertical {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 280px;
  height: auto;
}

@media (min-width: @tablet-width) {
  .advantage__item-vertical {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 160px;
    height: 205px;
  }
}

@media (min-width: @desktop-width) {
  .advantage__item-vertical {
    width: 290px;
    height: 205px;
  }
}

.advantage__div-vertical {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-shrink: 2;
  width: 230px;
  height: auto;
  margin-top: 5px;
}

@media (min-width: @tablet-width) {
  .advantage__div-vertical {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-shrink: 2;
    width: 165px;
    height: 109px;
  }
}

@media (min-width: @desktop-width) {
  .advantage__div-vertical {
    width: 254px;
    height: 100px;
    margin-top: 5px;

    &_gift {
      width: 218px;
    }

    &_burger {
      width: 290px;
    }
  }
}

.advantage__icon {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  width: 37px;
  height: 36px;
}

@media (min-width: @tablet-width) {
  .advantage__icon {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    width: 75px;
    height: 75px;
  }
}

.advantage__title {
  padding: 0;
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.4;
}

@media (min-width: @tablet-width) {
  .advantage__title {
    padding: 0;
    margin: 0;
    font-size: 21px;
  }
}

.advantage__comment {
  padding: 0;
  margin: 0;
  width: inherit;
  height: auto;
  font-weight: 400;
  line-height: 1.7;
  font-size: 14px;
}

@media (min-width: @tablet-width) {
  .advantage__comment {
    padding: 0;
    margin: 0;
    font-weight: 400;
    line-height: 1.7;
    font-size: 14px;
    text-align: center;
  }
}
